html, body {
    background-color: #fff;
    color: #636b6f;
    font-family: 'open sans Light', sans-serif;
    font-weight: 100;
    height: 100vh;
    margin: 0;
    margin: auto;

}

.border-top {
    border-top: 1px solid #dee2e6
}

.border-bottom {
    border-bottom: 1px solid #dee2e6
}

#photo-logo {
    height: 90px;
}

.position-ref {
    position: relative;
}

.photo-banner {
    margin-top: 40px;
}

.photo-header {
    background: #fafafa;
}

.top-right {
    position: absolute;
    right: 10px;
    top: 18px;
}

.content {
    text-align: center;
}

.links > a {
    color: #636b6f;
    padding: 0 15px;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: .1rem;
    text-decoration: none;
    text-transform: uppercase;
}

.photo-banner > a {
    color: #636b6f;
}

.photo-title {
    color: #333;
    font-size: 40px;
    font-weight: 300;
    text-alight: center;
    line-height: 81px;
}

.photo-content {
    margin: auto;
    max-width: 978px;
}

.photo-body {
    margin-top: 70px;
}

.photo-nav {
    line-height: 35px;
}

.photo-footer {
    margin-top: 35px;
}

.container {
    margin: auto;
    max-width: 978px;
    text-align: center;
}

.container > p {
    max-width: 500px;
    margin: auto;
    text-align: center;
}

.container > img {
    margin-top: 35px;
}

.m-b-md {
    margin-bottom: 30px;
}